
.outerContainer {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}
table {
  text-align: left;
}
table thead tr th:nth-child(1) {
    width: 20%;
}
table thead tr th:nth-child(2) {
    width: 15%;
}
table thead tr th:nth-child(3) {
    width: 45%;
}
table thead tr th:nth-child(4) {
    width: 20%;
}
table .table th, table .table td {
    padding-right: 20px;
}
table thead tr {
    background-color: rgba(221, 221, 221, 0.5);
}
table tbody tr.main:hover {
    cursor: pointer;
    background-color: rgba(255, 206, 133, 0.2);
    color: #000;
}
table tbody tr.main:hover .fa.fa-chevron-right {
      color: #333;
}
table .fa.fa-chevron-right {
    color: #aaa;
}
.opened {
  background-color: rgba(255, 206, 133, 0.2);
  color: #000;
}
.subDescription {
  background-color: rgba(238, 238, 238, 0.2);
}
.subDescription ul {
    margin: 0px;
    padding: 0px;
}
.subDescription ul li {
      list-style: none;
      font-size: 16px;
      padding: 10px;
}
.subDescription ul li .sub {
        text-decoration: underline;
        font-weight: 600;
}



