
.Pagination {
  padding-left: 20px;
  padding-right: 20px;
}
.paginationBar ul {
  margin: 0px;
  padding: 0px;
}
.paginationBar ul li {
    list-style: none;
    display: inline-block;
    border: solid 1px rgba(153, 153, 153, 0.5);
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
    margin-right: 5px;
}
.paginationBar ul li .button {
      width: 40px;
      height: 40px;
      padding-top: 8px;
      padding-bottom: 8px;
      text-align: center;
      font-size: 16px;
      font-weight: 900;
}
.is-info {
  background-color: #4074fa;
  color: #fff;
  font-weight: lighter;
}
.is-disable {
  color: rgba(119, 119, 119, 0.2);
}



